html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;font-family:'Roboto', sans-serif;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1.5;min-width:300px}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{background:#2d2d2d;color:white;margin:5%}h1{font-size:32px;line-height:150%;font-weight:bolder;text-align:center}h2{font-size:20px;text-align:center;line-height:2}a{color:#66bfc4}.description{margin-top:20px;margin-bottom:20px;display:flex;justify-content:space-between;box-sizing:border-box;width:100%;border:solid #66bfc4 2px;padding:15px;border-radius:10px}nav{margin-top:20px;margin-bottom:20px;display:flex;justify-content:space-between;box-sizing:border-box;width:100%;border:solid #66bfc4 2px;padding:15px;border-radius:10px}#analytics-container{box-sizing:border-box;width:100%;border:solid #66bfc4 2px;padding:15px;border-radius:10px}#analysis-type-selector input[type="radio"]{opacity:0;position:fixed;width:0}#analysis-type-selector label{display:inline-block;color:#66bfc4;background-color:#ddd;padding:5px 20px;font-size:16px;border:2px solid #66bfc4;color:#25a4ab;border-radius:4px}#analysis-type-selector input[type="radio"]:checked+label{background-color:#66bfc4;border-color:#25a4ab;color:white}#analysis-type-selector label:hover{background-color:#66bfc4;color:white;cursor:pointer}#year-selector{display:flex;flex-direction:column}#year-selector select{padding:5px 20px;-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:0;background:#ddd;width:100%;height:100%;color:#25a4ab;cursor:pointer;border:2px solid #66bfc4;border-radius:4px}#year-selector select option{color:#25a4ab;font-size:16px;text-align:center}
